import {
    Component, OnInit, Input, QueryList, ViewChildren, HostBinding,
    ElementRef, Renderer2, Directive, AfterContentInit, ViewChild
} from '@angular/core';


@Component({
    selector: 'farrisui-layout,farris-layout,layout',
    templateUrl: 'layout.component.html',
    styles: [
        `
        :host.layout-fill {position: absolute; top: 0; right: 0;bottom: 0;left: 0;}
        `
    ],
    encapsulation: ViewEncapsulation.Emulated
})
export class LayoutComponent implements OnInit, AfterContentInit {

    @Input() width: number;
    @Input() height: number;
    @Input() showBorder = true;

    // h: æ°´å¹³æå; v: åç´æå
    @Input() direction: 'h' | 'v' = 'h';

    @Input() fill = false;

    @HostBinding('class') myclass = 'd-flex';

    panels: LayoutPanelComponent[] = [];

    private ngZone: NgZone;

    constructor(private el: ElementRef, private render2: Renderer2, private injector: Injector) {
        this.ngZone = this.injector.get(NgZone);
    }

    ngOnInit() {
        this.init();
    }

    ngAfterContentInit() {
        this.checkPanels();

        this.panels.forEach(p => {
            if (p.split) {
                let margin = '';
                switch (p.region) {
                    case 'north':
                        margin = 'marginTop';
                        break;
                    case 'south':
                        margin = 'marginBottom';
                        break;
                    case 'west':
                        margin = 'marginLeft';
                        break;
                    case 'east':
                        margin = 'marginRight';
                        break;
                }
                this.render2.setStyle(this.getPanel('center').el.nativeElement, margin, '5px');
            }
        });
        this.ngZone.runOutsideAngular(() => {
            setTimeout(() => this.setPanelMaxSize());
        });
    }

    private init() {
        if (this.direction === 'h') {
            this.myclass += ' flex-row';
        } else {
            this.myclass += ' flex-column';
        }

        if (this.fill) {
            if (this.isInLayoutPanel()) {
                this.myclass += ' flex-fill';
                this.render2.addClass(this.el.nativeElement.parentElement, 'd-flex');
            } else {
                this.myclass += ' layout-fill';
            }
        } else {
            if (this.width) {
                this.render2.setStyle(this.el.nativeElement, 'width', this.width + 'px');
            }

            if (this.height) {
                this.render2.setStyle(this.el.nativeElement, 'height', this.height + 'px');
            }
        }
    }

    private getLayoutSize() {
        const size = {
            width: this.el.nativeElement.clientWidth,
            height: this.el.nativeElement.clientHeight
        };
        return size;
    }

    setPanelMaxSize() {
        const layoutSize = this.getLayoutSize();
        this.panels.forEach(p => {
            if (p.region !== 'center') {
                const minWidth = p.minWidth || 100;
                const minHeight = p.minHeight || 100;
                if (p.region === 'west' && layoutSize.width) {
                    const east = this.getPanel('east');
                    if (!east) {
                        p.maxWidth = layoutSize.width - minWidth;
                    } else {
                        p.maxWidth = layoutSize.width - east.width - minWidth;
                    }
                }

                if (p.region === 'east' && layoutSize.width) {
                    const west = this.getPanel('west');
                    if (!west) {
                        p.maxWidth = layoutSize.width - minWidth;
                    } else {
                        p.maxWidth = layoutSize.width - west.width - minWidth;
                    }
                }

                if (p.region === 'north' && layoutSize.height) {
                    const south = this.getPanel('south');
                    if (!south) {
                        p.maxHeight = layoutSize.height - minHeight;
                    } else {
                        p.maxHeight = layoutSize.height - south.height - minHeight;
                    }
                }

                if (p.region === 'south' && layoutSize.height) {
                    const north = this.getPanel('north');
                    if (!north) {
                        p.maxHeight = layoutSize.height - minHeight;
                    } else {
                        p.maxHeight = layoutSize.height - north.height - minHeight;
                    }
                }
            }
        });
    }

    getPanel(region: string) {
        return this.panels.find(p => p.region === region);
    }

    private checkPanels() {
        if (this.panels && this.panels.length) {
            const regions = this.panels.map(p => p.region);
            if (this.unique(regions).length === regions.length) {
                if (this.direction === 'h') {
                    if (regions.find(r => r === 'north' || r === 'south')) {
                        throw new Error('æ°´å¹³æ¹åæåæ¶ï¼åªè½åå« east æè west');
                    }
                } else {
                    if (regions.find(r => r === 'west' || r === 'east')) {
                        throw new Error('æ°´å¹³æ¹åæåæ¶ï¼åªè½åå« north æè south');
                    }
                }
                return true;
            } else {
                throw new Error('layout å¸å±å®¹å¨ä¸­å·æéå¤çpanelã');
            }
        }

        return true;
    }

    private unique(arr: string[]) {
        const tmp = new Set(arr);
        return Array.from(tmp);
    }

    private isInLayoutPanel() {
        return !!this.el.nativeElement.parentElement.attributes['layout-panel'];
    }
}


